/**
 * Display a table containing the current devices 
 *
 * @file      DeviceTable.jsx.
 * @author    Martin Lopez, Diego Moscoso, Jacob Summerville.
 * @since     11/29/2021
 * @copyright Martin Lopez, Diego Moscoso, Jacob Summerville 2021
 */

import axios from 'axios';
import React, { useState, useEffect } from "react";
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import DeleteIcon from '@mui/icons-material/Delete';
// import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import {Link} from 'react-router-dom'

import ServerHost from './../Server.jsx'
import LocalUserInit from './../common/LocalUser.jsx'

import "./../../styles/theme.css";
import "./../../styles/device.css";

/*
 * Name     : DeviceTable
 * Purpose  : Contains the device table to display devices
 * 
 * @param   : None
 * @return  : None
 */
const DeviceTable = () => {
    let   [user, setUser] = useState(LocalUserInit);
    const [devices, setDevices] = useState([]);

    useEffect(() => {
        const loggedInUser = localStorage.getItem("user");
        if (loggedInUser) {
            const foundUser = JSON.parse(loggedInUser);
            setUser(foundUser);
            setDevices(foundUser.devices);
        } else {
            window.location.href = "intro";
        }
    }, []);

    const DeleteRow = async (delId) => {
        let newDeviceList = [];

        const makeNewDeviceList = new Promise((resolve, reject) => {
            for (let i in devices) {
                if (devices[i].id !== delId) {
                    newDeviceList.push(devices[i]);
                }

                if (parseInt(i) === devices.length - 1) {
                    resolve('');
                }
            }
        });

        makeNewDeviceList
            .then(async () => { 
                setDevices(newDeviceList);

                user.devices = newDeviceList;

                const response = await axios.post(
                    ServerHost + "/device/update",
                    user
                );

                if (response.data === 'OK') { 
                    // store the user in localStorage
                    localStorage.setItem('user', JSON.stringify(user))
                } else {
                    alert('Unable to update device list');
                }
            })
    }

    const TableContent = (props) => {
        if (props.devices.length < 1 || props.devices === undefined) {
            return (
                <TableBody>
                    <TableRow
                      key='0'
                      sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                    >
                      <TableCell component="th" scope="row"></TableCell>
                      <TableCell>No Devices</TableCell>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                    </TableRow>
                </TableBody>
            )
        } else {
            return (
                <TableBody>
                  {props.devices.map((curDevice) => (
                    <TableRow
                      key={curDevice.name}
                      sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                    >
                      <TableCell component="th" scope="row">
                        {curDevice.name}
                      </TableCell>
                      <TableCell>{curDevice.id}</TableCell>
                      <TableCell>{curDevice.token}</TableCell>
                      <TableCell align="right">
                        <Button 
                            className="deviceBtn" 
                            variant="contained" 
                            size="small"
                            onClick={(() => DeleteRow(curDevice.id))}>
                            <DeleteIcon fontSize="small"/>
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
            )
        }
    }

    return (
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell>ID</TableCell>
                <TableCell>Token</TableCell>
                <TableCell align="right">
                  <Link to="/adddevice">
                    <Button className="deviceBtn" variant="contained" size="small">
                      <AddIcon fontSize="small"/>
                    </Button>
                  </Link>
                </TableCell>
              </TableRow>
            </TableHead>
            <TableContent devices={devices}/>
          </Table>
        </TableContainer>
    );
}
 
 export default DeviceTable;