import React from 'react';
import { useState, useEffect } from "react";
import countries from 'i18n-iso-countries';
import 'bootstrap/dist/css/bootstrap.css';
import LocalUserInit from '../common/LocalUser.jsx'
import "./../../styles/dashboard.css";

countries.registerLocale(require('i18n-iso-countries/langs/en.json'));

const CurrentWeather = () => {
    // State
    const [apiData, setApiData] = useState({});
    const [state, setState] = useState('85713');

    //API Key and URL
    const apiUrl = `https://api.openweathermap.org/data/2.5/weather?zip=${state}&appid=8bf8d3734951f5b35f9e9dc5e60fe334`;

    // Side effect
    useEffect(() => {
        const loggedInUser = localStorage.getItem("user");
        if (loggedInUser) {
            const foundUser = JSON.parse(loggedInUser);

            if (foundUser.zipcode !== undefined) {
                setState(foundUser.zipcode);
            } else {
                setState('85713');
            }
        } 
        fetch(apiUrl)
        .then((res) => res.json())
        .then((data) => setApiData(data));
    }, [apiUrl]);

    const kelvinToFarenheit = (k) => {
        return ((k - 273.15)  * (9/5) + 32).toFixed(1);
        
    };

    return (
    <div className="dashCompDiv">
        <h5>Current Weather</h5>
        
     
        <div className="container">
            <div>
                {apiData.main ? (
                    <div className="card-body text-center">
                        <img
                            src={`http://openweathermap.org/img/w/${apiData.weather[0].icon}.png`}
                            alt="weather status icon"
                            className="weather-icon"
                        />

                        <p className="h2">
                            {kelvinToFarenheit(apiData.main.temp)}&deg; F
                        </p>

                        <p className="h5">
                            <i className="fas fa-map-marker-alt"></i>{' '}
                            <strong>{apiData.name}</strong> <br/>
                            {' '}
                            {countries.getName(apiData.sys.country, 'en', {
                                select: 'official',
                            })}
                        </p>

                        <div className="row mt-4">
                            <div className="col-md-6">
                                <p>
                                    <i className="fas fa-temperature-low "></i>{' '}
                                    <strong>
                                        Low: {kelvinToFarenheit(apiData.main.temp_min)}&deg; F
                                    </strong>
                                </p>
                                <p>
                                    <i className="fas fa-temperature-high"></i>{' '}
                                    <strong>
                                        High: {kelvinToFarenheit(apiData.main.temp_max)}&deg; F
                                    </strong>
                                </p>
                            </div>
                            <div className="col-md-6">
                                <p>
                                    {' '}
                                    <strong>{apiData.weather[0].main}</strong>
                                </p>
                                <p>
                                    <i className = ""></i>{' '}
                                    <strong>
                                       Humidity:{apiData.main.humidity}%
                                    </strong>
                                </p>
                            </div>
                        </div>
                    </div>
                ) : 
                    (
                        <h3>Loading</h3>
                    )
                }
            </div>
        </div>  
    </div>
    )
}

export default CurrentWeather

