/**
 * Allow the user to enter their login credentials and verify
 * with the server
 *
 * @file      Login.jsx.
 * @author    Martin Lopez, Diego Moscoso, Jacob Summerville.
 * @since     11/29/2021
 * @copyright Martin Lopez, Diego Moscoso, Jacob Summerville 2021
 */

import axios from 'axios';
import React, { useState } from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import LoginIcon from '@mui/icons-material/Login';
import { Link } from 'react-router-dom';

import ServerHost from './../components/Server.jsx'

import "./../styles/theme.css";
import "./../styles/login.css";

const credInit = {
    username  : '', 
    password  : ''
}

/*
 * Name     : Login
 * Purpose  : Contains the main component for the login page
 * @param   : None
 * @return  : None
 */
const Login = () => {

    const [credentials, setCreds] = useState(credInit);

    const updateCreds = (key, value) => {
        setCreds(prevCreds => ({
            ...prevCreds,
            [key]: value
        }));
    };

    // login the user
    const handleSubmit = async e => {
        e.preventDefault();

        // send the username and password to the server
        const response = await axios.post(
            ServerHost + "/user/login",
            credentials
        );

        if (response.data.code === 'OK') {

            let userSave = { 
                username : credentials.username,
                fname    : response.data.fname,
                lname    : response.data.lname,
                zipcode  : response.data.zipcode,
                devices  : response.data.devices
            };

            // store the user in localStorage
            localStorage.setItem('user', JSON.stringify(userSave))

            window.location.href = "dashboard";
        } else if (response.data.code === 'Incorrect password') {
            alert('Incorrect Username or Password');
        } else if (response.data.code === 'Incorrect Username') {
            alert('Incorrect Username or Password');
        } else {
            alert('There was an error logging in');
        }
    };

    return (
        <div className="loginContainer">
            <h2 className="sectionHeader"> Login </h2>

            <Box
                className="foregroundForm"
                component="form"
                noValidate
                autoComplete="off"
                onSubmit={handleSubmit}
            >

                <div className="promptDiv">
                    <TextField
                        required
                        sx={{ m: 1, width: '90%' }}
                        id="username"
                        label="Email Address"
                        defaultValue={credentials.username}
                        onChange={({ target }) => updateCreds('username', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <TextField
                        required
                        sx={{ m: 1, width: '90%' }}
                        type="password"
                        className="passwordInput"
                        id="password"
                        label="Password"
                        defaultValue={credentials.password}
                        onChange={({ target }) => updateCreds('password', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <Button variant="contained" 
                        type="submit" 
                        endIcon={<LoginIcon/>}>
                        Login
                    </Button>
                </div>
            </Box>

            <div id="createDiv">
                <Link id="createAccount" to="/register">Create a new account</Link>
            </div>

            <div id="createDiv">
                <Link id="createAccount" to="/">View the Project and Team</Link>
            </div>
        </div>
    );
}

export default Login;