import React from 'react';
import Accordion from 'react-bootstrap/Accordion'

import iconMartin from './../img/icon-martin.jpg'
import iconDiego  from "./../img/icon-diego.jpg";
import iconJake   from "./../img/icon-jake.jpg";

import "./../styles/theme.css";
import "./../styles/intro.css";

const linkedInMartin = "https://www.linkedin.com/in/martin-manuel-lopez-94725442/";
const linkedInDiego  = "https://www.linkedin.com/in/diego-moscoso/";
const linkedInJake   = "https://www.linkedin.com/in/jacob-summerville-b604b9163/";

let accordionEventKey = 0;

const Bio = (props) => {
    return (
        <div className="col-lg-4 mb-4">
            <div className="card">
                <img src={props.icon} className="card-img-top" alt={'photo:' + props.name}/>
                <div className="card-body">
                    <a href={props.linkedin}><p className="card-text">{props.name}</p></a>
                    <p className="card-text">{props.netid}</p>
                </div>
             </div>
        </div> 
    );
}

const AccordionTab = (props) => {
    const tab = (
        <Accordion.Item eventKey={accordionEventKey}>
            <Accordion.Header>{props.header}</Accordion.Header>
            <Accordion.Body>{props.body}</Accordion.Body>
        </Accordion.Item>
    );
    accordionEventKey += 1;
    return tab;
}

const Home = () => {

    return (
        <div className="descContainer">
            <h1 className="mainHeader"> Homi - A Smart Home Hub </h1>

            <div className="descDiv">
                <p className="sectionHeader"> The smart home application is a low-cost IoT enabled 
                    web application </p>

                <Accordion>
                    <AccordionTab 
                        header="Monitoring Temperature and Humidity"
                        body="Utilizing the state of the art sensors throughout your Home, Homi's solution provides customers the capability 
                        of monitoring the temperature of each room by just logging in and adjusting the temperature. Homi's solution permits 
                        homeowners the features needed to control and manage temperature and humidity."/>
                    <AccordionTab 
                        header="Controlling a Thermostat"
                        body="Homi's thermostat solution provides user the capabilities of controlling your home's temperature remotely. With the state
                        of the art Internet of Thing (IoT) solution, homeowners can utilize the Homi App to login and control the temperature during
                        the winter, summer, and any time."/>
                    <AccordionTab 
                        header="Lighting"
                        body="Homi's Smart Hub provides homeowners the capability of dimming, turning on. or turning off their lighting throughout their
                        home. Homeowners now can develop and time when they want to dim, shut on, or shut off their lights throughout their home."/>
                    <AccordionTab 
                        header="Security"
                        body="Homi's Smart Hub provides homeowners the capability of securing their home through the security features. Homeowners now have
                        the peace of mind to secure their homes with the state of the art security monitoring system. The Homi Smart Hub provides homeonwers
                        the capability of knowing when their doors are closed or opened."/>
                </Accordion>
            </div>

            <hr/>

            <h1 className="mainHeader">The Team</h1>
            <div className="container">
                <div className="row">
                    <Bio name='Diego Moscoso' icon={iconDiego} linkedin={linkedInDiego}
                        netid='18dmoscoso'/> 
                    <Bio name='Martin M Lopez' icon={iconMartin} linkedin={linkedInMartin}
                        netid='lopez9'/>       
                    <Bio name='Jake Summerville' icon={iconJake} linkedin={linkedInJake}
                        netid='jsummerville'/>           
                </div>
             </div>
        </div>
    );
}

export default Home;