import React from 'react';
import {BrowserRouter as Router} from 'react-router-dom';
import {Routes, Route} from 'react-router-dom';

import Dashboard    from './pages/Dashboard.jsx';
import Devices      from './pages/Devices.jsx';
import Home         from './pages/Home.jsx';
import Login        from './pages/Login.jsx';
import Logout       from './pages/Logout.jsx';
import PageNotFound from './pages/PageNotFound.jsx';
import Profile      from './pages/Profile.jsx';
import Reference    from './pages/Reference.jsx';
import Register     from './pages/Register.jsx';

import Header         from './components/common/Header.jsx'
import Footer         from './components/common/Footer.jsx';
import AddDevice      from './components/devices/AddDevice.jsx';
import UpdateDevice   from './components/devices/UpdateDevice.jsx';
import CurrentWeather from './components/dashboard/CurrentWeather.jsx';
import ChartTemp      from './components/dashboard/ChartTemp.jsx';
import Demo           from './pages/Demo.jsx';

import './styles/theme.css'

const App = () => {
    return (
        <div className="mainDiv">
            <Router>
                <Header />
                <Routes>
                    <Route path="/"             element={<Home/>}/>
                    <Route path="login"         element={<Login/>}/>
                    <Route path="logout"        element={<Logout/>}/>
                    <Route path="register"      element={<Register/>}/>
                    <Route path="reference"     element={<Reference/>}/>
                    <Route path="dashboard"     element={<Dashboard/>}/>
                    <Route path="currweather"   element={<CurrentWeather/>}/>
                    <Route path="charttemp"     element={<ChartTemp/>}/>
                    <Route path="devices"       element={<Devices />}/>
                    <Route path="AddDevice"     element={<AddDevice/>}/>
                    <Route path='updatedevice'  element={<UpdateDevice/>}/>
                    <Route path="profile"       element={<Profile/>}/>
                    <Route path="demo"          element={<Demo/>}/>
                    <Route path="*"             element={<PageNotFound/>}/>
                </Routes>
            <Footer />
            </Router>
        </div>
    );
}

export default App;