import React from "react";

/*
 * Name     : Logout
 * Purpose  : Contains the main component for logging out
 * @param   : None
 * @return  : None
 */
const Logout = () => {
    localStorage.clear();
    window.location.href = "/";
    return <> </>
}

export default Logout;