/**
 * Register a new user and save credentials for future use
 *
 * @file      Register.jsx.
 * @author    Martin Lopez, Diego Moscoso, Jacob Summerville.
 * @since     11/29/2021
 * @copyright Martin Lopez, Diego Moscoso, Jacob Summerville 2021
 */

import axios from 'axios';
import React, { useState } from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import SendIcon from '@mui/icons-material/Send';

import ServerHost from './../components/Server.jsx'

import "../styles/theme.css";
import "../styles/create.css";

const emailRE = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
const passwordRE = /(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/;

const userInit = {
    fname     : '',
    lname     : '',
    username  : '', 
    password  : '',
    cpassword : '',
    zipcode   : ''
}

/*
 * Name     : Create
 * Purpose  : Contains the main component for the 
 *            account creation page
 * 
 * @param   : None
 * @return  : None
 */
const Create = () => {

    const [newUser, setNewUser] = useState(userInit);

    const updateUser = (key, value) => {
        setNewUser(prevUser => ({
            ...prevUser,
            [key]: value
        }));
    };

    const errorChecks = () => {
        if (newUser.fname === '') {
            alert('First name is empty');
            return true
        }

        if (newUser.lname === '') {
            alert('Last name is empty');
            return true
        }

        if (!emailRE.test(newUser.username.toLowerCase())) {
            alert('Email needs to be formatted correctly');
            return true
        }

        if (!passwordRE.test(newUser.password)) {
            alert('Password does not meet criteria');
            return true
        }

        if (newUser.password !== newUser.cpassword) {
            alert("Passwords don't match");
            return true
        }

        return false;
    }

    // log the user in
    const handleSubmit = async e => {
        e.preventDefault();

        updateUser('username', newUser.username.toLowerCase());

        let formError = errorChecks();

        if (formError) return;

        // send the username and password to the server
        const response = await axios.post(
            ServerHost + "/user/create",
            newUser
        );

        if (response.data === 'OK') {
            let userSave = { 
                username : newUser.username, 
                fname    : newUser.fname,
                lname    : newUser.lname,
                zipcode  : newUser.zipcode, 
                devices  : []
            };
            
            // store the user in localStorage
            localStorage.setItem('user', JSON.stringify(userSave))

            window.location.href = "dashboard";
        } else if (response.data === 'Username already taken.') {
            alert('Username taken');
        } else {
            alert('There was an error creating the account');
        }
    }

    return (
        <div className="createContainer">
            <h2 className="sectionHeader"> Create your account</h2>

            <Box
                className="foregroundForm"
                component="form"
                noValidate
                autoComplete="off"
                onSubmit={handleSubmit}
            >

                <div className="promptDiv">
                    <TextField
                        required
                        sx={{ m: 1, width: '90%' }}
                        id="fname"
                        label="First Name"
                        defaultValue={newUser.fname}
                        onChange={({ target }) => updateUser('fname', target.value)}
                    />

                    <TextField
                        required
                        sx={{ m: 1, width: '90%' }}
                        id="lname"
                        label="Last Name"
                        defaultValue={newUser.lname}
                        onChange={({ target }) => updateUser('lname', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <TextField
                        required
                        error={newUser.username !== "" && !emailRE.test(newUser.username.toLowerCase())}
                        helperText={(newUser.username !== "" && !emailRE.test(newUser.username.toLowerCase())) ? "Email incorrectly formatted" : ""}
                        sx={{ m: 1, width: '90%' }}
                        id="username"
                        label="Email Address"
                        defaultValue={newUser.username}
                        onChange={({ target }) => updateUser('username', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <TextField
                        required
                        sx={{ m: 1, width: '90%' }}
                        id="zipCode"
                        label="Zip Code"
                        type="number"
                        defaultValue={newUser.zipcode}
                        onChange={({ target }) => updateUser('zipcode', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <TextField
                        required
                        error={newUser.password !== newUser.cpassword && !passwordRE.test(newUser.password)}
                        helperText={(newUser.password !== '' && !passwordRE.test(newUser.password)) ? "Password does not meet criteria" : ''}
                        sx={{ m: 1, width: '90%' }}
                        type="password"
                        id="password"
                        label="Password"
                        defaultValue={newUser.password}
                        onChange={({ target }) => updateUser('password', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <TextField
                        required
                        error={newUser.password !== newUser.cpassword}
                        helperText={newUser.password !== newUser.cpassword ? "Passwords don't match" : ''}
                        sx={{ m: 1, width: '90%' }}
                        type="password"
                        id="cpassword"
                        label="Confirm Password"
                        defaultValue={newUser.cpassword}
                        pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
                        onChange={({ target }) => updateUser('cpassword', target.value)}
                    />
                </div>

                <div id="passReq">
                    <ul id="passReqList">
                        <p> Password Requirements </p>
                        <li>Uppercase Letter</li>
                        <li>Lowercase Letter</li>
                        <li>Number</li>
                        <li>Special Character</li>
                        <li>At least 8 characters</li>
                    </ul>
                </div>

                <div className="promptDiv">
                    <Button variant="contained" 
                        type="submit" 
                        endIcon={<SendIcon/>}>
                        Register
                    </Button>
                </div>
            </Box>
            <div className="emptyDiv"></div>
        </div>
    );
}

export default Create;