import axios from 'axios';
import React, { useState, useEffect } from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import AddIcon from '@mui/icons-material/Add';

import ServerHost from './../Server.jsx'

import LocalUserInit from './../common/LocalUser.jsx'

import "./../../styles/theme.css";
import "./../../styles/create.css";

const deviceInit = {
    name: '',
    id: '',
    token: ''
}

/*
 * Name     : AddDevice
 * Purpose  : Contains the main component for adding a new device
 * 
 * @param   : None
 * @return  : None
 */

const AddDevice = () => {
    const [user, setUser] = useState(LocalUserInit);
    const [device, setDevice] = useState(deviceInit);

    useEffect(() => {
        const loggedInUser = localStorage.getItem("user");
        if (loggedInUser) {
            const foundUser = JSON.parse(loggedInUser);
            setUser(foundUser);
        } else {
            window.location.href = "/";
        }
    }, []);

    const updateDevice = (key, value) => {
        setDevice(prevDevice => ({
            ...prevDevice,
            [key]: value
        }));
    };

    const handleSubmit = async e => {
        e.preventDefault();

        for (let curDevice of user.devices) {
            if (curDevice.name === device.name) {
                alert('Device name is already in use');
                return;
            } else if (curDevice.id === device.id) {
                alert('Device ID is already in use');
                return;
            }
        }

        user.devices.push(device);

        const response = await axios.post(
            ServerHost + "/device/update",
            user
        );

        if (response.data === 'OK') { 
            // store the user in localStorage
            localStorage.setItem('user', JSON.stringify(user))
            window.location.href = "/devices";
        } else {
            alert('Unable to add device');
        }
    };

    return (
        <div className="createContainer">
            <h2 className="sectionHeader"> Add New Device</h2>

            <Box
                className="foregroundForm"
                component="form"
                noValidate
                autoComplete="off"
                onSubmit={handleSubmit}
            >

                <div className="promptDiv">
                    <TextField
                        required
                        sx={{ m: 1, width: '90%' }}
                        id="outlined-required"
                        label="Name"
                        defaultValue={device.name}
                        onChange={({ target }) => updateDevice('name', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <TextField
                        required
                        sx={{ m: 1, width: '90%' }}
                        id="outlined-required"
                        label="ID"
                        defaultValue={device.id}
                        onChange={({ target }) => updateDevice('id', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <TextField
                        required
                        sx={{ m: 1, width: '90%' }}
                        id="outlined-required"
                        label="Token"
                        defaultValue={device.token}
                        onChange={({ target }) => updateDevice('token', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <Button variant="contained" 
                        type="submit" 
                        endIcon={<AddIcon/>}>
                        Add Device
                    </Button>
                </div>
            </Box>
            <div className="emptyDiv"></div>
        </div>
    )
}

export default AddDevice

