import React from "react";
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import Tooltip from '@mui/material/Tooltip';

import "./../../styles/theme.css";
import "./../../styles/device.css";

const webhookUrl = 'https://ec2-3-139-236-201.us-east-2.compute.amazonaws.com:3000/particle/report';

/*
 * Name     : Webhooks
 * Purpose  : Contains the device related webhooks
 * 
 * @param   : None
 * @return  : None
 */
const Webhooks = () => {
    return (
       <div className="deviceWebhooks">

            <h2 className="webhookSectionHeader">Webhook Set-Up</h2>

            <List dense={true}>
                <ListItem disablePadding>
                    <ListItemButton>
                        <ListItemText primary="Event Name: homidata" />
                    </ListItemButton>
                </ListItem>
                <ListItem disablePadding>
                    <ListItemButton>
                        <Tooltip title="Click to Copy" placement="right" arrow>
                            <button 
                                id='webhookUrl'
                                onClick={() => navigator.clipboard.writeText(webhookUrl)}
                            >
                                URL: {webhookUrl}
                            </button>
                        </Tooltip>
                    </ListItemButton>
                </ListItem>
                <ListItem disablePadding>
                    <ListItemButton>
                        <ListItemText primary="Request Type: POST" />
                    </ListItemButton>
                </ListItem>
                <ListItem disablePadding>
                    <ListItemButton>
                        <ListItemText primary="Request Format: JSON" />
                    </ListItemButton>
                </ListItem>
                <ListItem disablePadding>
                    <ListItemButton>
                        <ListItemText primary="Device: Any" />
                    </ListItemButton>
                </ListItem>
                <ListItem disablePadding>
                    <ListItemButton>
                        <ListItemText primary="Status: Enabled" />
                    </ListItemButton>
                </ListItem>
            </List>
        </div>
    );
}
 
 export default Webhooks;