import React from 'react'
import faker from 'faker'

const labels = ['Saturday', 'Friday', 'Thursday', 'Wednesday', 'Tuesday', 'Monday', 'Sunday'];

export const lowTempData  = labels.map(() => faker.datatype.number({ min: 60, max: 70 }));
export const highTempData = labels.map(() => faker.datatype.number({ min: 75, max: 90 }));
export const humidityData = labels.map(() => faker.datatype.number({ min: 20, max: 60 }));

const DailyTemp = () => {
    return (
        <div className="dashCompDiv">
            <h5>Last Week's Weather</h5>

            <table>
                <tbody>
                    <tr>
                        <th>Day</th>
                        <th>Low</th>
                        <th>High</th>
                        <th>RH</th>
                    </tr>

                    <tr>
                        <td>Yesterday</td>
                        <td>{lowTempData[0]}&deg;F</td>
                        <td>{highTempData[0]}&deg;F</td>
                        <td>{humidityData[0]}%</td>
                    </tr>
                    <tr>
                        <td>{labels[1]}</td>
                        <td>{lowTempData[1]}&deg;F</td>
                        <td>{highTempData[1]}&deg;F</td>
                        <td>{humidityData[1]}%</td>
                    </tr>
                    <tr>
                        <td>{labels[2]}</td>
                        <td>{lowTempData[2]}&deg;F</td>
                        <td>{highTempData[2]}&deg;F</td>
                        <td>{humidityData[2]}%</td>
                    </tr>
                    <tr>
                        <td>{labels[3]}</td>
                        <td>{lowTempData[3]}&deg;F</td>
                        <td>{highTempData[3]}&deg;F</td>
                        <td>{humidityData[3]}%</td>
                    </tr>
                    <tr>
                        <td>{labels[4]}</td>
                        <td>{lowTempData[4]}&deg;F</td>
                        <td>{highTempData[4]}&deg;F</td>
                        <td>{humidityData[4]}%</td>
                    </tr>
                    <tr>
                        <td>{labels[5]}</td>
                        <td>{lowTempData[5]}&deg;F</td>
                        <td>{highTempData[5]}&deg;F</td>
                        <td>{humidityData[5]}%</td>
                    </tr>
                    <tr>
                        <td>{labels[6]}</td>
                        <td>{lowTempData[6]}&deg;F</td>
                        <td>{highTempData[6]}&deg;F</td>
                        <td>{humidityData[6]}%</td>
                    </tr>
                </tbody>
            </table>

        </div>
    )
}

export default DailyTemp
