/**
 * Display current user profile settings and allow edits
 *
 * @file      Profile.jsx.
 * @author    Martin Lopez, Diego Moscoso, Jacob Summerville.
 * @since     11/29/2021
 * @copyright Martin Lopez, Diego Moscoso, Jacob Summerville 2021
 */

import axios from 'axios';
import React, { useState, useEffect } from "react";

import ServerHost from './../components/Server.jsx'

import "../styles/theme.css";
import "../styles/profile.css";

const userInit = {
    fname     : '',
    lname     : '',
    username  : '', 
    password  : '',
    zipcode   : '',
    devices   : []
}

/*
 * Name     : Profile
 * Purpose  : Contains the main component for the profile page
 * @param   : None
 * @return  : None
 */
const Profile = () => {

    const [user, setUser] = useState(userInit)
    const [curUser, setCurUser] = useState(userInit)

    const updateUser = (key, value) => {
        setUser(prevUser => ({
            ...prevUser,
            [key]: value
        }));
    };

    const updateCurUser = (key, value) => {
        setCurUser(prevUser => ({
            ...prevUser,
            [key]: value
        }));
    };
    
    useEffect(() => {
        const loggedInUser = localStorage.getItem("user");
        if (loggedInUser) {
            const foundUser = JSON.parse(loggedInUser);
            updateUser('fname', foundUser.fname)
            updateUser('lname', foundUser.lname)
            updateUser('username', foundUser.username)
            updateUser('zipcode', foundUser.zipcode)
            updateUser('devices', foundUser.devices)
            updateCurUser('fname', foundUser.fname)
            updateCurUser('lname', foundUser.lname)
            updateCurUser('username', foundUser.username)
            updateCurUser('zipcode', foundUser.zipcode)
            updateCurUser('devices', foundUser.devices)
        } else {
            window.location.href = "/";
        }
    }, []);

    const handleSubmit = async e => {
        e.preventDefault();

        let userObj = {
            curUsername: curUser.username,
            curPassword: curUser.password
        };

        if (user.username && curUser.username !== user.username 
            && user.username !== '') {
            userObj.newUsername = user.username;
            updateCurUser('username', user.username);
        }

        if (user.password && curUser.password !== user.password && user.password !== '') {
            userObj.newPassword = user.password;
        }

        if (user.fname && curUser.fname !== user.fname && user.fname !== '') {
            userObj.fname = user.fname;
            updateCurUser('fname', user.fname);
        }

        if (user.lname && curUser.lname !== user.lname && user.lname !== '') {
            userObj.lname = user.lname;
            updateCurUser('lname', user.lname);
        }

        // send the username and password to the server
        const response = await axios.post(
            ServerHost + "/user/update",
            userObj
        );

        if (response.data.code === 'OK') {
            let userSave = { 
                username : curUser.username, 
                fname    : curUser.fname,
                lname    : curUser.lname,
                zipcode  : curUser.zipcode, 
                devices  : curUser.devices
            };

            // set the state of the user
            setUser(response.data.code)

            // store the user in localStorage
            localStorage.setItem('user', JSON.stringify(userSave))

            updateCurUser('password', '');
            updateUser('password', '');

            alert('Account Updated');

        } else {
            alert(response.data.code);
        }
    };

    return (
        <div className="profileContainer">
            <h2 className="profileSectionHeader"> Profile</h2>

            <form className="profileForegroundDiv" onSubmit={handleSubmit}>

                <div className="promptDiv">
                    <label className="promptLabel" htmlFor="usernameInput">Username</label>
                    <input
                        type="text"
                        id="usernameInput"
                        value={user.username}
                        placeholder="Username"
                        onChange={({ target }) => updateUser('username', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <label className="promptLabel" htmlFor="fnameInput">First Name</label>
                    <input
                        type="text"
                        id="fnameInput"
                        value={user.fname}
                        placeholder="First Name"
                        onChange={({ target }) => updateUser('fname', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <label className="promptLabel" htmlFor="lnameInput">Last Name</label>
                    <input
                        type="text"
                        id="lnameInput"
                        value={user.lname}
                        placeholder="Last Name"
                        onChange={({ target }) => updateUser('lname', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <label className="promptLabel" htmlFor="newPasswordInput">New Password</label>
                    <input
                        type="password"
                        id="newPasswordInput"
                        value={user.password}
                        placeholder="New Password"
                        onChange={({ target }) => updateUser('password', target.value)}
                    />
                </div>

                <div className="promptDiv">
                    <label className="promptLabel" htmlFor="curPasswordInput">Current Password</label>
                    <input
                        type="password"
                        id="curPasswordInput"
                        value={curUser.password}
                        placeholder="Current Password"
                        onChange={({ target }) => updateCurUser('password', target.value)}
                    />
                </div>

                <button id="saveBtn" type="submit">Save</button>
            </form>
        </div>
    );
}

export default Profile;
