import React from 'react';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import faker from 'faker';
 
ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
);
 
export const options = {
    responsive: true,
    plugins: {
        legend: {
            position: 'top', // as const,
        },
        title: {
            display: true,
            text: 'Temperature & Humidity History',
        },
        options: {
            maintainAspectRatio: false,
        }
    },
};
 
const labels = ['12:00 AM', '1:00 AM', '2:00 AM', '3:00 AM', '4:00 AM', '5:00 AM', '6:00 AM', '7:00 AM', '9:00 AM', '10:00 AM', '11:00 AM', '12:00 PM',
                '1:00 PM', '2:00 PM', '3:00 PM', '4:00 PM', '5:00 PM', '6:00 PM', '7:00 PM', '8:00 PM', '9:00 PM', '10:00 PM', '11:00 PM' ];
 
 export const data = {
   labels,
   datasets: [
     {
       label: 'Temperature',
       data: labels.map(() => faker.datatype.number({ min: 60, max: 90 })),
       borderColor: 'rgb(255, 99, 132)',
       backgroundColor: 'rgba(255, 99, 132, 0.5)',
     },
     {
       label: 'Humidity',
       data: labels.map(() => faker.datatype.number({ min: 20, max: 60 })),
       borderColor: 'rgb(53, 162, 235)',
       backgroundColor: 'rgba(53, 162, 235, 0.5)',
     },
   ],
 };
 
const ChartTemp = () => {
    return (
        <div className="dashCompDiv">
            <div id="chartDiv">
                <Line options={options} data={data} />
            </div>
        </div>
    )
}

export default ChartTemp
